import React, { useEffect } from 'react';
import ReactGA from 'react-ga';
import { createBrowserHistory } from 'history';
import { FaHome } from 'react-icons/fa';

import { PortalLayoutStyled, NavTitle, NavItem } from './PortalLayout.styles';

const PortalLayout = ({ children }) => {
  return (
    <PortalLayoutStyled>
      <div className="layout--main">
        <div className="layout--navbar">
          <div className="navbar--title" >
            <NavTitle to="/">Hui Ying</NavTitle>
          </div>
          <div className="navbar--item">
            <NavItem to="/latest">
              Latest
            </NavItem>
            <NavItem to="/tech-world">
              Tech World
            </NavItem>
            <NavItem to="/book-reviews">
              Book Reviews
            </NavItem>
            <NavItem to="/chinese-culture">
              Chinese Culture
            </NavItem>
            <NavItem to="/globe_trotting">
              Globe Trotting
            </NavItem>
            <NavItem to="/quotes">
              Quotes
            </NavItem>
            <NavItem to="/reflections">
              Reflections
            </NavItem>
          </div>
        </div>
        <div className="layout--children">{children}</div>
        <div className="layout--footer">

        </div>
      </div>
    </PortalLayoutStyled>
  );
};
export default PortalLayout;
